import {  Component,  Vue, Prop} from 'vue-property-decorator';

@Component({})
export default class ConfirmDialog extends Vue {
  @Prop() textConfirm?: string;

  private mounted() {
    
  }

  private cancel() {
    this.$emit('cancel');
  }

  private confirm() {
    this.$emit('confirm');
  }

}
